import React from 'react';
import { Row, Col, Input, Button} from 'antd';
import GGEditor, { Flow } from 'gg-editor';
import { withRouter } from 'react-router-dom';

import { FlowContextMenu } from '../../process/components/EditorContextMenu';
import { FlowToolbar } from '../../process/components/EditorToolbar';
import { FlowItemPanel } from '../../process/components/EditorItemPanel';
import CustomNode from '../../process/components/node/nodeTensorflow';
import StartNode from '../../process/components/node/nodeStart';
import styles from './process.module.less'

@withRouter
class CreateProcess extends React.Component {

  constructor(props){
    super(props);
    
    this.state = {
    }
  }

  componentWillMount(){
    
  }
  
  componentWillUnmount(){
    
  }
  
  listenCommand = ({command}) => { 
    if(command.name === 'add' && command.type === 'node'){
      const {componentType, ggeditorObjectString, x: addX, y: addY} = command.addModel
      if(componentType && componentType === 'process'){  // 添加一个流程组件
        this.flow.remove(command.addId)
        if(ggeditorObjectString){
          const process = JSON.parse(ggeditorObjectString)
          const {addId} = command
          console.log(process)
          const {nodes, edges} = process
          let minX = nodes ? nodes[0].x : 0
          let minY = nodes ? nodes[0].y : 0
          nodes && nodes.forEach(node=>{
            if(node.x < minX) minX = node.x
            if(node.y < minY) minY = node.y
          })
          nodes && nodes.forEach(node=>{
            console.log(this.flow)
            this.flow.add('node', {...node, id: addId + node.id, x: node.x-minX+addX, y: node.y-minY+addY})
          })
          edges && edges.forEach(edge=>{
            const {id, ...model} = edge
            this.flow.add('edge', {...model, source: addId+model.source, target: addId+model.target})
          })
        }
      }
    }
  }

  render() {
    return (<div className={styles.canvasContainer}>
        <GGEditor 
          className={styles.editor}
          onAfterCommandExecute={this.listenCommand}
        >
          {/* <Button>保存自定义流程组件</Button> */}
          <Row type="flex" className={styles.editorBd} style={{height:'100%'}}>
            <Col span={6} className={styles.editorSidebar}>
            <Input.Search
              placeholder="搜索组件"
              style={{margin: '5px 0', width: '95%', textAlign: 'center', alignSelf: 'center'}}
              enterButton
              size="middle"
              onSearch={value => console.log(value)}
            />
              <FlowItemPanel custom='no'/>
            </Col>
            <Col span={18} className={styles.editorContent}>
              <FlowToolbar save='no' />
              <CustomNode />
              <StartNode />
              <Flow 
                className={styles.flow}
                data={this.state.processObj}
                ref={component => {
                  if (component) {
                    console.log('ref',component)
                    this.flow = component.page;
                  }
                }}
              />
            </Col>
          </Row>
          <FlowContextMenu />
        </GGEditor>
   </div> );
  }
};


export default CreateProcess;
