import React, {Component} from 'react'
import {Radio} from 'antd'
import CreateProcess from './createProcess';

class CreateCustomComponent extends Component{
    constructor(props){
        super(props);
        
        this.state = {
            componentType: '',
        }
    }

    changeComponentType = (e) => {
        this.setState({
            componentType: e.target.value
        })
    }

    render(){
        const {componentType} = this.state
        return(
            <div>
                <Radio.Group value={componentType} onChange={this.changeComponentType}>
                    <Radio value='process'>流程组件</Radio>
                    <Radio value='algorithm'>算法组件</Radio>
                    <Radio value='model'>模型组件</Radio>
                </Radio.Group>
                {
                    componentType === 'process' && <CreateProcess></CreateProcess>
                }
            </div>
        )
    }
}

export default CreateCustomComponent